import { Reducer } from 'react';
import { IProofCollectionProps } from 'utils/interfaces';

export type ProofCollectionReducerAction =
  | { type: 'GET_PROOF_COLLECTION_START' }
  | { type: 'GET_PROOF_COLLECTION_COMPLETED' }
  | { type: 'GET_PROOF_COLLECTION_SUCCESS'; items: IProofCollectionProps[] }
  | { type: 'GET_PROOF_COLLECTION_FAILED'; message?: string; code?: string };

export type TInitialStates = {
  loading: boolean;
  error: {
    isError: boolean;
    message?: string;
    code?: string;
  };
  items: IProofCollectionProps[];
};

export const ProofOfCollectionReducer: Reducer<TInitialStates, ProofCollectionReducerAction> = (state, action) => {
  switch (action.type) {
    case 'GET_PROOF_COLLECTION_START': {
      return {
        ...state,
        loading: true,
      };
    }
    case 'GET_PROOF_COLLECTION_SUCCESS': {
      return {
        ...state,
        loading: false,
        items: action.items,
      };
    }
    case 'GET_PROOF_COLLECTION_COMPLETED': {
      return {
        ...state,
        loading: false,
      };
    }
    case 'GET_PROOF_COLLECTION_FAILED': {
      return {
        ...state,
        loading: false,
        error: {
          isError: true,
          message: action.message,
          code: action.code,
        },
      };
    }

    default:
      return state;
  }
};
