import React, { useEffect, useMemo, useReducer, useState } from 'react';
import { CarouselSlider, ContentPlaceHolder, ItemDetailsDialog } from 'components';
import { Bags } from './components';
import proofCollectionService from 'services/ProofCollectionService';
import { InfoCodeParser, getCodeFromError, useQuery } from 'utils';
import { ProofOfCollectionReducer, TInitialStates } from '../proofOfCollectionReducer';
import ErrorMessage from 'components/errorMessage';
import styled from 'styled-components';
const initialState: TInitialStates = {
  items: [],
  loading: true,
  error: {
    isError: false,
  },
};

interface IPickupImagesProps {
  pickupImageBags: string;
  pickupImageAllItems: string;
  pickupImageDoor: string;
  pickupImageRejectedRecycling: string;
}
interface IWhenWhereDetailsProps {
  pickupAddress: {
    geofenceLabel: string;
    addressType: string;
    community: string;
    number: string;
    building?: string;
    street?: string;
    villa?: string;
  };
  pickupDurationText: string;
  isCollectAtDoor: boolean;
}
const PickupImages: React.FC<IPickupImagesProps> = ({ pickupImageBags, pickupImageAllItems, pickupImageDoor, pickupImageRejectedRecycling }) => {
  const [selectedSlideIndex, setSelectedSlideIndex] = useState<number>(0);
  const [showImgDialog, setShowImgDialog] = useState<boolean>(false);
  const handleImageClick = (val: number) => {
    setSelectedSlideIndex(val);
    setShowImgDialog(!showImgDialog);
  };
  const createItem = (url: string, reason: string) => (url ? [{ url, reason }] : []);

  const items = [
    ...createItem(pickupImageAllItems, 'Everything at Door'),
    ...createItem(pickupImageDoor, 'Door Image'),
    ...createItem(pickupImageBags, 'Bags'),
    ...createItem(pickupImageRejectedRecycling, 'Rejected Recycling'),
  ];
  return (
    <>
      <BgImageParent>
        <CarouselSlider onPress={handleImageClick} data={items} />
      </BgImageParent>
      <ItemDetailsDialog
        headerTitle="Collection Images"
        data={items}
        isShowImagesOnly={true}
        show={showImgDialog}
        onHide={() => setShowImgDialog(false)}
        selectedSlideIndex={selectedSlideIndex}
      />
    </>
  );
};

const WhenWhereDetails = ({ pickupAddress, pickupDurationText, isCollectAtDoor }: IWhenWhereDetailsProps) => {
  const { addressType, number, building, street, villa } = pickupAddress;
  const pickupText = pickupDurationText.replace(/AM|PM/gi, (matched) => matched.toLowerCase());

  const Item = ({ name, value }: { name: string; value: string }) => (
    <div className="d-flex">
      <WhenWhereKey>{name}:</WhenWhereKey>
      <WhenWhereValue>{value} </WhenWhereValue>
    </div>
  );
  return (
    <div className="mt-0 mb-4">
      <Item name="Where" value={addressType !== 'Villa' ? building + ', ' + number : street + ', ' + villa} />
      <Item name="When" value={pickupText} />
      <Item name="How" value={isCollectAtDoor ? 'At door' : 'In person'} />
    </div>
  );
};
const ProofOfCollection: React.FC = () => {
  const [
    {
      items,
      loading,
      error: { isError, message, code },
    },
    dispatch,
  ] = useReducer(ProofOfCollectionReducer, initialState);

  const apiCalling = async () => {
    try {
      dispatch({ type: 'GET_PROOF_COLLECTION_START' });
      const { orderPickupsDetail } = await proofCollectionService.getProofCollection();
      dispatch({ type: 'GET_PROOF_COLLECTION_SUCCESS', items: orderPickupsDetail });
    } catch (error) {
      if (error.isAxiosError) {
        let message = 'unknown.error'; // default message
        message = error.response ? InfoCodeParser(error.response.data.infoCodes) : error.message;
        const code = getCodeFromError(error);
        dispatch({ type: 'GET_PROOF_COLLECTION_FAILED', message, code });
      }
    } finally {
      dispatch({ type: 'GET_PROOF_COLLECTION_COMPLETED' });
    }
  };
  useEffect(() => {
    apiCalling();
  }, []);

  const renderItems = useMemo(() => {
    if (loading) return <ContentPlaceHolder amount={3} />;
    if (isError && !loading && !items?.length) return <ErrorMessage code={code} message={message} />;
    return (
      items &&
      items?.map((data, index) => {
        const {
          pickupImageBags,
          pickupImageAllItems,
          pickupImageDoor,
          pickupAddress,
          pickupDurationText,
          isCollectAtDoor,
          hasRecycling,
          recyclingRejected,
          pickupImageRejectedRecycling,
        } = data;
        const itemCount = index + 1;
        return (
          <React.Fragment key={index}>
            <ProofOfCollectionHeadingText>Proof of Collection {itemCount > 1 ? itemCount : ''} </ProofOfCollectionHeadingText>
            <WhereWhenContainer>
              <WhenWhereDetails pickupAddress={pickupAddress} pickupDurationText={pickupDurationText} isCollectAtDoor={isCollectAtDoor || false} />
            </WhereWhenContainer>
            <PickupImages
              pickupImageBags={pickupImageBags}
              pickupImageAllItems={pickupImageAllItems}
              pickupImageDoor={pickupImageDoor}
              pickupImageRejectedRecycling={hasRecycling && recyclingRejected ? pickupImageRejectedRecycling : ''}
            />
            <Bags data={data} />
          </React.Fragment>
        );
      })
    );
  }, [items, loading, isError]);

  return <Container color="#291482">{renderItems}</Container>;
};

const Container = styled.div`
  flex: 1;
  padding-top: 0px;
  color: ${(props) => props.color};
  width: 100%;
  height: 100vh;
  line-height: 24px;
  letter-spacing: 0.4px;
  font-family: 'Inter Light';
  margin: 0px;
  padding: 0 24px;
`;
const WhereWhenContainer = styled.div`
  display: flex;
`;

const BgImageParent = styled.div`
  margin: 0px 0px 35px 0px;
`;

const WhenWhereKey = styled.span`
  color: ${(props) => props.theme.primaryBlue};
  color: #291482;
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 8px;
  letter-spacing: 0.1px;
  font-weight: 600;
`;

const WhenWhereValue = styled.span`
  color: ${(props) => props.theme.primaryBlue};
  font-family: 'Inter Regular';
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 8px;
  letter-spacing: 0.1px;
`;

const ProofOfCollectionHeadingText = styled.p`
  font-family: 'Inter H1 Bold';
  padding-top: 16px;
  font-size: 20px;
  margin: 0px;
  padding-bottom: 24px;
`;

export default ProofOfCollection;
