import React from 'react';
import { withRouter, Route, RouteComponentProps, BrowserRouter as Router, Switch, Redirect } from 'react-router-dom';
import { ProofCollection } from './pages';
import './proofOfCollectionRoute.scss';
interface Props extends RouteComponentProps {}

const ItemOverview: React.FC<Props> = ({ match: { path } }) => {
  return (
    <Switch>
      <Route path={`${path}`} children={<ProofCollection />} />
      <Redirect to={path} />
    </Switch>
  );
};

export default withRouter(ItemOverview);
