import React from 'react';

import { ReactComponent as CpBag } from 'assets/images/icons/cp-new.svg';
import { ReactComponent as WfBag } from 'assets/images/icons/wf-new.svg';
import { ReactComponent as HcBag } from 'assets/images/icons/hc-new.svg';
import { ReactComponent as PbBag } from 'assets/images/icons/pb-new.svg';
import { ReactComponent as ScBag } from 'assets/images/icons/sc-bag.svg';
import { ReactComponent as TfBag } from 'assets/images/icons/tf-bag.svg';
import { ReactComponent as RecylingBag } from 'assets/images/icons/recycling-bag.svg';
import './bags.scss';
interface IBagsProps {
  data: {
    totalPBags: number;
    totalCPBags: number;
    totalHCBags: number;
    totalWFBags: number;
    totalSCBags: number;
    totalTFBags: number;
    pickupImageRejectedRecycling?: string;
    hasRecycling?: boolean;
    recyclingRejected?: boolean;
    serviceLine?: string;
  };
}
interface IBagItemsProps {
  name: string;
  image?: JSX.Element;
  count?: number;
  totalBagsCollected?: boolean;
}
const Bags = ({ data }: IBagsProps) => {
  const { totalPBags, totalCPBags, totalHCBags, totalWFBags, hasRecycling, recyclingRejected, totalSCBags, totalTFBags, serviceLine } = data;

  const Item = ({ name, image, count, totalBagsCollected }: IBagItemsProps) => {
    if (count === 0) return <></>;
    return (
      <div className={totalBagsCollected ? 'total-bags-collected-container' : 'item-bag-container'}>
        <div className="accordion-left-component"> {image}</div>
        <div className="item-bag-text">{name}</div>
        <div className="accordion-right-component"> {count} </div>
      </div>
    );
  };

  if (!data) return <></>;

  const isShoeCare = serviceLine === 'shoes';
  const isFinery = serviceLine === 'finery';
  return (
    <div className="item-bag-body">
      {!isShoeCare && !isFinery && (
        <>
          <Item name="Clean & Press" image={<CpBag />} count={totalCPBags} />
          <Item name="HomeCare" image={<HcBag />} count={totalHCBags} />
          <Item name="Wash & Fold" image={<WfBag />} count={totalWFBags} />
          <Item name="Press Only" image={<PbBag />} count={totalPBags} />

          {hasRecycling && !recyclingRejected && <Item name="Recycling" image={<RecylingBag />} />}
          <Item totalBagsCollected name="Total Bags Collected" count={totalPBags + totalCPBags + totalHCBags + totalWFBags} />
        </>
      )}

      {isShoeCare && (
        <>
          <Item name="Shoe Care" image={<ScBag />} count={totalSCBags} totalBagsCollected={false} />
        </>
      )}
      {isFinery && (
        <>
          <Item name="Finery" image={<TfBag />} count={totalTFBags} totalBagsCollected={false} />
        </>
      )}
    </div>
  );
};

export default Bags;
